<template>
    <div class="cb">
        <div class="cba">
            <textarea name="pinglun"
            rows="3" 
            maxlength="150" 
            autofocus 
            class="textareacss"
            placeholder="150"
            v-model="pinglunvlu">
            </textarea>
        </div>
        <div>

        </div>
        <div class="caozuo">
            <div class="biaoqing">
                <a href="javascript:;">
                    <img src="./imgall/face.png" alt="" />
                    <span @click="tianjia"></span>
                </a>
            </div>
            <button class="pinglun" @click="postacomment"></button>
        </div>
        <hr>


        <!-- ///////////////////////////////////////////////////// -->
        <cc class="cc" style="margin-top:-30px;"  v-show="show"></cc>
        <!-- ///////////////////////////////////////////////////// -->


        <div v-if="commbox.length">
            <div class="cc" v-for="(item,index) of commbox" :key="index">
                <div  class="ccimage">
                    <a href="javascript:;">
                        <!-- <img :src="'./imgall/'+item.userimage" alt="" /> -->
                        <img :src="`${item.userimage}`" alt="" />
                        <span>{{item.username}}:</span>
                    </a>
                </div>
                <div class="ccinner">
                    <!--  -->
                    {{item.content}}
                </div>
            </div>
        </div>
        <div class="dd" v-else></div>
    </div>
</template>

<style scoped>
.cb .pinglun{
    width: 120px;
    border: 0;
    outline: none;
    border-radius: 10px;
    background-color: #bfbfbf;
}




.cb .cc{
    margin: auto;
    padding-bottom: 30px;
    padding-top: 50px;
    width: 800px;
}
.cb .dd{
    margin: auto;
    padding-bottom: 30px;
    padding-top: 50px;
    width: 800px;
}
.cb .cc a>img{
    width: 60px;
    height: 60px;
    border-radius: 25%;
    /*  */
    object-fit:cover;
}
.cb .cc a{
    display: flex;
}
.cb .cc div>a>span{
    align-self: center;
}
.cb .ccinner{
    padding-top: 20px;
}






.cb .pinglun:hover{
    background-color: #e8e8e8;
}
.cb .pinglun:active{
    color: #bfbfbf;
}

.cb .huizan button{
    width: 30px;
    height: 30px;
    margin-right: 5px;
    border: 0;
    outline: none;
    background-color: #fff;
}
.cb .huizan{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cb a{
    text-decoration: none;
    color: #000;
}
.cb .biaoqing>a{
    display: flex;
    align-items: center;
}
.cb .caozuo{
    margin: auto;
    padding-top: 50px;
    width: 800px;
    display: flex;
    justify-content: space-between;
}
.cb .cba .textareacss{
    width: 100%;
    resize: none;
    border: none;
    padding: 0;
    overflow: hidden; 
    border-radius: 5px;
    outline: none;
    border:1px solid #bfbfbf;
    color:#000;
    padding-left:10px;
    font-size:16px;
}
.cb .cba{
    margin: auto;
    padding-top: 50px;
    width: 800px;
    display: flex;
    justify-content: center;
}
</style>

<script>
import cc from './cc';
export default {
    components: {
        cc
    },
    data(){
        return {
            show:false,
            //
            //
            commbox:[],
            //
            pinglunvlu:"",
            //username
            //ID
            username:''
        }
    },
    mounted(){
        //  commbox
        this.axios.get('/commall').then(res=>{
            let data=res.data.results;
            //data
            data.forEach(item => {
                //
                item.userimage=require('./imgall/'+item.userimage);
            });

            this.commbox=data;
        })
    },
    methods:{
        //
        tianjia(){
            this.show=!this.show;
        },
        postacomment(){
            //axios.defaults.baseURL="http//xx.xx.com";
            //
            //axios.get("/xx").then(result=>{console.log(result.data);})
            //xx
            let xx=this.pinglunvlu;
            //
            //value
            if(xx.length != 0 ){
                //->->
                console.log(":"+this.username+":"+xx+";......");
                //post
                this.axios.post('/commvalue','username='+this.username+'&userx='+this.pinglunvlu).then(res=>{
                    if(res.data.code==1){
                        //
                        location.reload()
                    }else{
                        //
                        this.$router.push('/reg');
                    }
                })
            }
        }
    },
}
</script>