<template>
  <el-tabs
    class="work"
    v-model="activeName"
    type="border-card"
    @tab-click="handleClick"
  >
    <el-tab-pane label="" name="first">
      <div class="check">
        <div class="user">
          <img
            src="../assets/forgetpassword/user.png"
            alt=""
            :class="uemail1"
          />
          <input
            placeholder=""
            type="email"
            v-model="email"
            :class="uemail2"
          />
          <button class="getemail" @click="getAuthCode()">{{ content }}</button>
        </div>
        <div class="email">
          <img src="../assets/forgetpassword/email.png" alt="" />
          <input
            type="text"
            placeholder=""
            v-model="emailVerificationCode"
          />
        </div>
        <div class="buttom">
          <button class="next" @click="toSecond"></button>
          <button class="next"></button>
        </div>
        <div class="online"></div>
      </div>
    </el-tab-pane>
    <el-tab-pane label="" name="second">
      <div class="check">
        <div class="user">
          <img src="../assets/forgetpassword/pwd.png" alt="" />
          <input name="pwd1" type="password" placeholder="" />
        </div>
        <div class="email">
          <img src="../assets/forgetpassword/pwd.png" alt="" />
          <input name="pwd1" type="password" placeholder="" />
        </div>
        <div class="buttom">
          <button class="next" @click="toThird"></button>
          <button class="next"></button>
        </div>
        <div class="online"></div>
      </div>
    </el-tab-pane>
    <el-tab-pane label="" name="third">
      <div class="check">
        <div class="succe">
          <img src="../assets/forgetpassword/suse.png" alt="" />
        </div>
        <div class="text"></div>
        <div class="buttom">
          <button class="next">
            <router-link to="/login"></router-link>
          </button>
        </div>
      </div>
    </el-tab-pane>
  </el-tabs>
</template>
<style>
* {
  margin: 0;
  padding: 0;
}
.work {
  background: white;
  width: 600px;
  border: 1px solid #e5e5e5;
  margin: 0 auto;
  text-align: center;
  margin-top: 3%;
  border-radius: 3px;
}

/*  */

.work .check {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
}
.work .user,
.work .email {
  width: 400px;
  height: 60px;
  background-color: #e5e5e5;
  margin-top: 30px;
  display: flex;
  justify-content: left;
  border-radius: 3px;
}
.work input {
  background-color: #e5e5e5;
  border: 0;
}
.work input:focus {
  outline: none;
}
.work img {
  width: 24px;
  height: 24px;
  margin-top: 18px;
  margin-right: 12px;
  margin-left: 12px;
}
.work button {
  border: 0;
  width: 150px;
  height: 40px;
  background-color: white;
  margin-top: 9px;
  margin-left: 12px;
  border-radius: 3px;
  color: #707070;
}
.work .disabled {
  background-color: #ddd;
  border-color: #ddd;
  color: #57a3f3;
  cursor: not-allowed;
}

/*  */
.work .succe img {
  width: 93px;
  height: 90px;
}
.work .text {
  color: #1296db;
  font-size: 48px;
}
.work .buttom {
  display: flex;
}
.work .next {
  width: 70px;
  height: 40px;
  border: 0;
  background-color: #1296db;
  border-radius: 3px;
  color: white;
  margin: 30px;
}
.work a {
  color: #fff;
}
.work a:hover {
  color: #fff;
  text-decoration: none;
}
</style>
<script>
export default {
  data() {
    return {
      email: "",
      emailVerificationCode: "",
      uemail1: "uemail",
      uemail2: "",
      activeName: "first",
      content: "", // 
      totalTime: 60, //
      canClick: true, //canClick
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
      tab.index + 1;
    },
    getAuthCode() {
      if (!this.canClick) {
        this.canClick = false;
      } else {
        this.axios.get("/send?uemail=" + this.email);
        window.alert("");
      }
      this.content = this.totalTime + "s"; //60
      let clock = window.setInterval(() => {
        this.totalTime--;
        this.content = this.totalTime + "s";
        if (this.totalTime < 0) {
          //0
          window.clearInterval(clock);
          this.content = "";
          this.totalTime = 60;
          this.canClick = true; //
        }
      }, 1000);
    },
    regEmail() {
      //
      let emailReg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (emailReg.test(this.email)) {
        this.uemail1 = "uemail";
        this.uemail2 = "";
        return true;
      } else {
        this.uemail1 = "uemail noemail";
        this.uemail2 = "noemail";
        return false;
      }
    },
    toSecond() {
      this.activeName = "second";
    },
    toThird() {
      this.activeName = "third";
    },
  },
};
</script>